*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

body{
  background-color: #fff;
  color: #444444;
}

header{
  width: 100%;
}

header > *:first-child{
  margin-left: 1em;
}

/* OVERFLOW SCROLL-BAR */

/* width */
::-webkit-scrollbar {
  width: 0.5em;
  height: 0.4em;
}

/* Track */
::-webkit-scrollbar-track {

  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: lightgray; 
  border-radius: 10px;
}

.App {
  text-align: center;
  height: 100vh;
}

input{
  all: unset;
  width: 80%;
  background-color: #EEE;
  padding: 0.5em;
  border-radius: 15px;
  text-align: left;
  color: gray;
}

input::placeholder{
  color: gray;
}

.lists{
  display: flex;
  justify-content: space-around;
  padding: 1.5em;
}

.lists > *{
  background-color: #fff;
  border: lightgrey 0.1em solid;
  border-radius: 1em;
  width: 60vw;
  max-width: 450px;
  /* min-height: 40vh; */
  padding: 1em;
}

@media(max-width: 800px){
  .lists > *{
    width: 100vw;
    max-width: 100vw;
  }
}

/* === FOOTER === */
footer{
  position: fixed;
  bottom: 0;
  width: 100%;
}